import React, { useEffect, useState, Fragment } from "react";
import Tabletop from "tabletop";

export default function App() {
  const [data, setData] = useState([]);
 
  useEffect(() => {
    Tabletop.init({
      key: "1nE5tYXoI8N2ffTB45Io6GhL0PFQrKBfEwz-EgtSyof0",
      simpleSheet: true
    })
      .then((data) => setData(data))
      .catch((err) => console.warn(err));
     
  }, []);

  return (
    <>
      <h1>data from google sheets</h1>
       <ul>

      {data.filter(data => data.SchoolName === "縣立宜蘭國中").sort(function(a,b){return parseInt(a.token) > parseInt(b.token) ?-1:1;}).map((item, i) => (
        <Fragment key={i}>

          <li>學校名稱 -- {item.SchoolName}</li>
          <li>名次 - {i+1} </li>
          <li>姓名 - {item.name}</li>
          <li>代幣 - {item.token}</li>
          <br />
        </Fragment>
      ))}
    </ul>
    </>
  );
}


